export default function Category(props) {
  return (
    <div className="d-flex flex-column gap-3 fs-5">
      <div className="fw-bold">Choose category</div>
      <select
        className="form-select bg-secondary bg-opacity-25 p-3 ps-2 fs-5"
        style={{
          border: "none",
          transition: "none",
        }}
        onChange= {(event) => { props.setStates({...props.states, "category": event.target.value })}}
        value={props.states["category"]}
      >
        <option value="">Choose</option>
        {props.categories.map((category => (
            <option value= {category}>{category}</option>
        )))}
      </select>
    </div>
  );
}
